.home {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro {
    max-width: 540px;
    text-align: center;
    z-index: 2;
}

.home img {
    margin-bottom: 1.5rem;
    width: 20%;
    border-radius: 10px;
}

.home__name {
    font-size: var(--hl-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials {
    display: flex;
    column-gap: 15rem;
    margin: 6rem 0;
}

.home__social-link {
    color: var(--title-color);
    font-size: 1.3rem;
    transition: 0.3s;
}

.home__social-link:hover {
    color: hsl(43, 100%, 68%);
}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
    color: var(--title-color);
}

.mouse {
    border: 2px solid #454360;
    display: block;
    height: 2.5rem;
    width: 1.5rem;
    margin: auto;
    margin-top: 0.75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0% {
        top: 29%;
    }

    15% {
        top: 50%;
    }

    50% {
        top: 50%;
    }

    100% {
        top: 29%;
    }
}

.wheel {
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

.shapes {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.shape {
    position: absolute;
}

@keyframes move {
    from {
        left: 50%;
        top: 50%;
        right: 50%;
        bottom: 50%;
        transform: scale(0.1);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

.s1 {
    animation: move 1s ease;
    left: 2%;
    top: 10%;
}

.s2 {
    animation: move 1.3s ease;
    left: 18%;
    top: 30%;
}

.s3 {
    animation: move 1.6s ease;
    left: 5%;
    bottom: 30%;
}

.s4 {
    animation: move 1.9s ease;
    left: 2%;
    bottom: 10%;
}

.s5 {
    animation: move 2.2s ease;
    left: 44%;
    top: 10%;
}

.s6 {
    animation: move 2.5s ease;
    left: 36%;
    bottom: 10%;
}

.s7 {
    animation: move 2.8s ease;
    top: 20%;
    right: 25%;
}

.s8 {
    animation: move 3.1s ease;
    right: 24%;
    bottom: 20%;
}

.s9 {
    animation: move 3.4s ease;
    right: 2%;
    top: 10%;
}

.s10 {
    animation: move 3.7s ease;
    top: 45%;
    right: 11%;
}

.s11 {
    animation: move 3.9s ease;
    right: 2%;
    bottom: 10%;
}

.shape:hover {
    scale: 2;
    transition: 1.5s;
    animation: rotateImg 1.5s ease-in-out infinite;
}

@keyframes rotateImg {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(1795deg);
    }
}

@media screen and (max-width: 1024px) {
    .home__social-link {
        font-size: 1.125rem;
    }
}